form,
label,
fieldset,
legend,
input,
select,
textarea,
button {
  margin: 0;
}

fieldset,
legend {
  padding: 0;
}

form,
details {
  @include type-leader;
  @include type-leader-clear;
  display: block;
}

details {
  @include border-radius(2px);
  border: 1px solid $color-grey-cement;
}

summary {
  background-color: $color-grey-cement;
  display: list-item;
  font-weight: 700;
  padding: em($base-vertical-margin / 2);

  &:focus {
    outline: none;
  }
}

@include forms-details-spacing;

details[open] summary {
  border-bottom: 1px solid $color-grey-cement;
}

@include forms-details-spacing;

label {
  @include type-leader(.5);
  @include type-leader-clear;
  display: block;
  font-weight: 700;
}

fieldset {
  @include type-leader;
  @include type-leader-clear;
  border: 0;
  padding: 0;
}

legend {
  @include type-leader;
  @include type-leader-clear;
  display: block;
  width: 100%;
}

@include input-size(medium);

#{$selector-form-text-inputs},
select,
textarea {
  @include box-sizing(border-box);
  @include border-radius(2px);
  @include appearance(none);
  @include type-leader-clear;
  @include input-placeholder {
    color: $color-grey-cement;
  }
  background-color: $color-white;
  border: 1px solid $color-grey-cement;
  display: block;
  font-family: inherit;
  max-width: 100%;
  overflow: hidden;
  width: auto;
}

select {
  background-image: url('data:image/svg+xml;base64,');
  background-repeat: no-repeat;
  background-size: 10px;

  &::-ms-expand {
    display: none;
  }
}

select[multiple] {
  background-image: none;
  height: auto;
}

textarea {
  height: auto;
  max-width: 100%;
  resize: vertical;
}

input[type='radio'],
input[type='checkbox'] {
  margin-right: .25em;
}
